@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body, #root, .App {
  height: 100%;
  background: black;
  color: white;
}

#root {
  --bg-color: black;
  --secondary-color: #ffda0a;
  --text-color: white;

  --page-LR-margin: 8vw;
}

.horizontal-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  flex-wrap: wrap;
  /* justify-content: center; */
  align-content: center;
}

button, a {
  cursor: pointer;
  background-color: transparent;
  border: none;
  font: inherit;
  font-weight: bold;
  color: inherit;
  padding: 0;
  transition: all 0.3s linear;
  text-decoration: none;
}

button.link:hover, a.link:hover{
  color: var(--secondary-color);
}

button.std-btn, a.std-btn {
  display: inline-block;
  background-color: var(--secondary-color);
  color: var(--bg-color);
  width: 10rem;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  text-align: center;
  padding: 1rem;
  border-radius: 10px;
  font-weight: bold;
  font-size: 1.3rem;
  transition: all 0.3s ease-in;
}

button.std-btn:hover, a.std-btn:hover {
  background-color: var(--text-color);
  /* box-shadow: 0 0 10px var(--secondary-color); */
}

h1 {
  font-size: 4.5rem;
  font-weight: bold;
  margin: 0;
  margin-bottom: 1rem;
}

h2 {
  font-size: 1.5rem;
  font-weight: bold;
  margin: 0;
  margin-bottom: 1rem;
}

.hflex {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}
/** HEADER **/

#header {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  padding: 2rem;
}

#name-title {
  font-size: 3rem;
  font-weight: bold;
}

.small-separator {
  height: 2px;
  width: 7rem;
  background-color: var(--text-color);
  margin: 0.5rem;
}

#header > nav {
  font-size: 1.5rem;
  font-weight: bold;
}

#header > nav > a.active {
  color: var(--secondary-color);
}

/** BODY **/

#body {
  /* height: 100%; */
  width: 100%;
}

.body-separator {
  height: 1.3rem;
  width: 13rem;
  background-color: var(--secondary-color);
}

.body-content {
  /* margin: 1.5rem var(--page-LR-margin); */
  padding: 1.5rem var(--page-LR-margin);
  /* margin-bottom: 0; */
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  height: 100%;
  width: 100%;
}

.page-content {
  font-size: 1.2rem;
}

.fade-appear, .fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-appear.fade-appear-active, .fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  transition: opacity 300ms ease-in;
}

.fade-appear h1, .fade-enter h1 {
  animation: slide-left-to-right ease-in-out 1000ms;
}

@keyframes slide-left-to-right {
  0% {
    transform: translateX(-5rem);
    opacity: 0;
  } 70% {
    opacity: 0.5;
  } 100% {
    transform: translateX(0);
    opacity: 1;
  }
}

.fade-appear > .body-content > *:not(h1),
.fade-enter > .body-content > *:not(h1) {
  animation: slide-bottom-to-top ease-in-out 1100ms;
}

@keyframes slide-bottom-to-top {
  0% {
    transform: translateY(5rem);
    opacity: 0;
  } 75% {
    opacity: 0.5;
  } 100% {
    transform: translateY(0);
    opacity: 1;
  }
}

div.transition-group {
  position: relative;
}

section.route-section {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

/** Home **/

#home > .page-content {
  max-width: min(30rem, calc(100vw - (var(--page-LR-margin) * 2)));
}

#home > ul {
  font-size: 1.5rem;
  font-weight: bold;
  padding-top: 1.5rem;
}

/** ABOUT **/

#about > .page-content {
  max-width: min(40rem, calc(100vw - (var(--page-LR-margin) * 2)));
}

.tech-stack img {
  height: 2.5rem;
  transition: transform 0.2s ease-in-out;
}

.tech-stack img:hover {
  transform: scale(1.5);
}

.tech-stack li {
  --scale: 0;
  position: relative;
}

.tech-stack li::after {
  position: absolute;
  bottom: -1rem;
  left: 50%;
  transform: translateX(-50%) translateY(100%) scale(var(--scale));
  transition: 0.2s transform ease-in-out;
  transform-origin: top center;
  content: attr(data-name);
  padding: 0.5rem;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  max-width: 100vw;
  background-color: white;
  color: black;
  border-radius: 7px;
  z-index: 1;
}

.tech-stack li:hover {
  --scale: 1;
}

/** CONTACT **/

#contact ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
}

#contact li img {
  height: 2rem;
}

#contact a {
  display: flex;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  font-weight: normal;
}

/** PROJECTS **/

#projects ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: auto;
  grid-gap: 4rem;
  gap: 4rem;
}

#projects li {
  border: 1px solid var(--text-color);
  border-radius: 7px;
}

#projects .thumbnail {
  height: 25rem;
}

#projects .thumbnail-title {
  font-size: 2.5rem;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 15rem;
  text-align: center;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  transition: all 0.3s ease-in-out;
}

#projects .thumbnail-description {
  background-color: var(--text-color);
  color: var(--bg-color);
  height: 10rem;
  padding: 2rem;
  border: none;
  border-bottom-left-radius: 5px; 
  border-bottom-right-radius: 5px; 
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
}

#projects .thumbnail-description > div:first-child {
  height: 4.5rem;
  overflow: hidden;
  border: none;
}

#projects .thumbnail-description > a:hover {
  color: inherit;
}

/** DEMO **/

.demo > ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto;
  justify-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.demo > ul img {
  width: 100%;
  object-fit: contain;
}

@media screen and (max-width: 1400px) {
  .demo > ul {  
    grid-template-columns: repeat(1, 1fr);
  }
}

.project-detail > .page-content {
  max-width: min(40rem, calc(100vw - (var(--page-LR-margin) * 2)));
}

.project-detail-snippet {
  font-style: italic; 
}

.project-detail ol {
  padding: 0.75rem;
}
